import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { withFormik } from "formik";
import * as Yup from "yup";
import { useHistory } from "react-router-dom";

import "./TeachingPlanEdit.css";

import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import PageHeader from "./components/PageHeader/PageHeader";
import teachingPlanApi from "api/common/teachingPlan";
import subMajorApi from "api/common/subMajor";
import {
  FaExchangeAlt,
  FaPlus,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { RoutesString } from "pages/routesString";
import { toast } from "react-toastify";

const initValues = {
  // maKeHoach: "",
  tenHocKy: "",
  sTT: 0,
  nganhDaoTao: 0,
};

const TeachingPlanEdit = function ({
  values,
  handleChange,
  resetForm,
  errors,
}) {
  const { t } = useTranslation();

  const history = useHistory();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [listSubMajor, setListSubMajor] = useState([
    { id: "", khoa: 0, nganhDaoTao: { ten: "" } },
  ]);

  const availableStatus = [
    {
      value: false,
      name: t("admin.common.AVAILABLE"),
    },
    {
      value: true,
      name: t("admin.common.DELETED"),
    },
  ];

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    } else {
      const res2 = await subMajorApi.getAll();
      if (res2) {
        setListSubMajor(res2.contents);
      }
    }
  }, []);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await teachingPlanApi.getById(id);
      if (res) {
        // initValues.maKeHoach = res.maKeHoach;
        values.tenHocKy = res.tenHocKy;
        values.sTT = res.sTT;
        res.nganhDaoTao?.id ? values.nganhDaoTao = Number(res.nganhDaoTao.id) : values.nganhDaoTao = null;
      }
      const res2 = await subMajorApi.getAll();
      if (res2) {
        setListSubMajor(res2.contents);
      } // eslint-disable-next-line
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  };
  const resetNoti = () => {
    setNotification({
      color: "success",
      isOpen: false,
      messages: [],
    });
  }

  const handleEditOnClick = async function (e) {
    e.preventDefault();
    resetNoti();
    try {
      if (isUpdatePage) {
        const res = await teachingPlanApi.putById(id, {...values, nganhDaoTao: Number(values.nganhDaoTao)});
        if (res) {
          toast.success(t("admin.UPDATE_SUCCESS"));
          updateForm(id);
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      } else {
        const res = await teachingPlanApi.post({...values, nganhDaoTao: Number(values.nganhDaoTao)});
        if (res) {
          toast.success(t("admin.ADD_SUCCESS"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } catch (err) {
      if (err?.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
            toast.error(t("ER_DUP_ENTRY"));
          }
          // else if(String(err?.response?.data?.error)?.includes("NGANHDAOTAO_NAME_EXIST")){
          //   toast.error(t("nganhdaotao.NGANHDAOTAO_NAME_EXIST"));
          // }
          else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          }
          else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  const handleRefreshOnClick = function (e) {
    e.preventDefault();
    setNotification({
      color: "success",
      isOpen: false,
      messages: [],
    });
    resetForm();
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    resetNoti();
    try {
      const res = await teachingPlanApi.deleteById(id);
      if (res) {
        if (res.status === 200 || String(res.message) === 'DELETE_KEHOACHGIANGDAY_SUCCESSFULLY') {
          toast.success(t("admin.common.DELETED"));
          history.push(RoutesString.ADMIN_TEACHING_PLAN_LIST)
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.teaching_plan_edit.UPDATE")
            : t("admin.teaching_plan_edit.ADD")
        }
        items={[
          {
            href: RoutesString.ADMIN_TEACHING_PLAN_LIST,
            title: t("admin.teaching_plan_list.LIST"),
          },
        ]}
      />

      <PageHeader
        headerTitle={
          isUpdatePage
            ? t("admin.teaching_plan_edit.UPDATE")
            : t("admin.teaching_plan_edit.ADD")
        }
      />

      <div className="page-content">
        <Form>
          <Notification
            color={notification.color}
            isOpen={notification.isOpen}
            messages={notification.messages}
          />
          {/* <FormGroup className="row">
            <Label for="value" className="col-lg-3">
              {t("admin.teaching_plan_edit.CODE")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaUserAlt />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="maKeHoach"
                  placeholder={t("admin.teaching_plan_edit.CODE_PLACEHOLDER")}
                  value={values.maKeHoach}
                  onChange={handleChange}
                  invalid={errors.maKeHoach}
                  readOnly={isUpdatePage}
                />
              </InputGroup>
              {errors.maKeHoach && (
                <FormText color="danger">{errors.maKeHoach}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br /> */}
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.teaching_plan_list.SEMESTER_NAME")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="tenHocKy"
                  placeholder={t(
                    "admin.teaching_plan_edit.SEMESTER_NAME_PLACEHOLDER"
                  )}
                  value={values.tenHocKy}
                  onChange={handleChange}
                  invalid={errors.tenHocKy}
                />
              </InputGroup>
              {errors.tenHocKy && (
                <FormText color="danger">{errors.tenHocKy}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.teaching_plan_list.STT")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="number"
                  name="sTT"
                  placeholder={t("admin.teaching_plan_edit.STT_PLACEHOLDER")}
                  value={values.sTT}
                  onChange={handleChange}
                  invalid={errors.sTT}
                />
              </InputGroup>
              {errors.sTT && <FormText color="danger">{errors.sTT}</FormText>}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.teaching_plan_list.SUB_MAJOR")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="select"
                  name="nganhDaoTao"
                  value={values.nganhDaoTao}
                  onChange={handleChange}
                >
                  <option key={1} value="0">
                    {t("admin.teaching_plan_edit.SUB_MAJOR_PLACEHOLDER")}
                  </option>
                  {listSubMajor.length > 0 &&
                    listSubMajor.map((item, index) => {
                      return (
                        <option key={index + 1} value={item.id}>
                          {/* {t(
                                        "admin.teaching_plan_list.SUB_MAJOR_COURSE"
                                    )}
                                    :  */}
                          {item.khoa} - {" "}
                          {/* {t("admin.training_major_list.NAME")}:{" "} */}
                          {item.nganhDaoTao?.ten} - {" "}
                          {item.nganhDaoTao?.chuongTrinhDaoTao?.maCTDT}
                        </option>
                      );
                    })}
                </Input>
              </InputGroup>
              {errors.nganhDaoTao && (
                <FormText color="danger">{errors.nganhDaoTao}</FormText>
              )}
            </div>
          </FormGroup>{" "}

          {/* {isUpdatePage && (
            <>
              <br />
              <FormGroup className="row">
                <Label for="isDeleted" className="col-lg-3">
                  {t("admin.common.AVAILABLE_STATUS")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaUsersCog />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="isDeleted"
                      value={values.isDeleted}
                      onChange={handleChange}
                    >
                      {availableStatus &&
                        availableStatus.map((item, index) => {
                          return (
                            <option key={index} value={item.value}>
                              {item.name}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>{" "}
            </>
          )} */}
          <br />

          <FormGroup>
            <Button color="success" onClick={handleEditOnClick}>
              {isUpdatePage ? (
                <>
                  <FaExchangeAlt /> {t("admin.common.UPDATE")}
                </>
              ) : (
                <>
                  <FaPlus /> {t("admin.common.ADD")}
                </>
              )}
            </Button>
            &emsp;
            {isUpdatePage && (
              <>
                <Button onClick={handleRefreshOnClick}>
                  <FaSync /> {t("admin.common.REFRESH")}
                </Button>
                &emsp;
                <Button color="danger" onClick={handleDeleteOnClick}>
                  <FaTrash /> {t("admin.common.DELETE")}
                </Button>
              </>
            )}
          </FormGroup>
        </Form>
      </div>
    </div>
  );
};

const TeachingPlanEditFormikForm = withFormik({
  mapPropsToValues() {
    return { ...initValues };
  },
  enableReinitialize: true,
  validationSchema: Yup.object().shape({
    maKeHoach: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
    tenHocKy: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
    sTT: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .positive(i18n.t("admin.POSITIVE_WARNING")),
    nganhDaoTao: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .positive(i18n.t("REQUIRED_FIELD_WARNING")),
  }),
})(TeachingPlanEdit);

export default TeachingPlanEditFormikForm;
